/**
 * Enemy unit types and interfaces for the TotalBattle Army Calculator
 * Simplified structure without player-specific resource management fields
 */

/**
 * Attack modifier interface for enemy units
 */
export interface EnemyAttackModifier {
  /** Type of unit this modifier targets (e.g., 'Melee', 'Flying', 'Mounted') */
  target_type: string;
  /** Type of modifier (typically 'Strength') */
  modifier_type: 'Strength';
  /** Absolute value of the modifier */
  value: number;
}

/**
 * Base enemy unit interface representing an enemy unit
 */
export interface EnemyUnit {
  /** Display name of the enemy unit */
  name: string;
  /** Array of unit type tags (e.g., ['Flying', 'Epic Monster']) */
  unit_types: string[];
  /** Unit health points */
  health: number;
  /** Unit attack strength */
  strength: number;
  /** Array of attack modifiers this unit has against other unit types */
  attack_modifiers?: EnemyAttackModifier[];
}

/**
 * User-created enemy unit stored in browser memory
 */
export interface UserEnemyUnit extends EnemyUnit {
  /** Unique identifier for user-created units */
  id: string;
  /** Timestamp when the unit was created */
  createdAt: Date;
  /** Timestamp when the unit was last modified */
  modifiedAt: Date;
}

/**
 * Enemy unit with calculated properties for battle simulation
 */
export interface EnemyUnitWithCalculatedProps extends EnemyUnit {
  /** Calculated strength per health ratio for comparison */
  readonly strengthPerHealth: number;
  /** Calculated total combat effectiveness score */
  readonly effectivenessScore: number;
}

/**
 * Enemy unit validation result
 */
export interface EnemyUnitValidationResult {
  /** Whether the enemy unit data is valid */
  isValid: boolean;
  /** Array of validation error messages */
  errors: string[];
  /** Array of validation warning messages */
  warnings?: string[];
}

/**
 * Enemy unit filter criteria
 */
export interface EnemyUnitFilter {
  /** Filter by unit types (must have at least one) */
  unitTypes?: string[];
  /** Minimum strength requirement */
  minStrength?: number;
  /** Maximum strength requirement */
  maxStrength?: number;
  /** Minimum health requirement */
  minHealth?: number;
  /** Maximum health requirement */
  maxHealth?: number;
  /** Filter by name (partial match) */
  nameFilter?: string;
}

/**
 * Summary statistics for enemy units
 */
export interface EnemyUnitSummary {
  /** Total number of enemy units */
  totalUnits: number;
  /** Count of units by type */
  byUnitType: Record<string, number>;
  /** Strength range across all enemy units */
  strengthRange: {
    min: number;
    max: number;
    average: number;
  };
  /** Health range across all enemy units */
  healthRange: {
    min: number;
    max: number;
    average: number;
  };
}

/**
 * Utility class for enemy unit operations
 */
export class EnemyUnitUtils {
  /**
   * Calculate strength per health ratio
   */
  static getStrengthPerHealth(unit: EnemyUnit): number {
    return unit.health > 0 ? unit.strength / unit.health : 0;
  }

  /**
   * Calculate effectiveness score based on strength and health
   */
  static getEffectivenessScore(unit: EnemyUnit): number {
    // Simple effectiveness calculation: strength * health / 1000
    return (unit.strength * unit.health) / 1000;
  }

  /**
   * Check if enemy unit has specific unit type
   */
  static hasUnitType(unit: EnemyUnit, unitType: string): boolean {
    return unit.unit_types.some(type => 
      type.toLowerCase() === unitType.toLowerCase()
    );
  }

  /**
   * Get attack modifier value against specific target type
   */
  static getAttackModifierAgainst(unit: EnemyUnit, targetType: string): number {
    if (!unit.attack_modifiers) return 0;
    
    const modifier = unit.attack_modifiers.find(mod => 
      mod.target_type.toLowerCase() === targetType.toLowerCase()
    );
    
    return modifier ? modifier.value : 0;
  }

  /**
   * Calculate total strength including modifiers against target
   */
  static getTotalStrengthAgainst(unit: EnemyUnit, targetType: string): number {
    const baseStrength = unit.strength;
    const modifier = EnemyUnitUtils.getAttackModifierAgainst(unit, targetType);
    return baseStrength + modifier;
  }

  /**
   * Validate enemy unit data structure
   */
  static validateEnemyUnit(unit: Partial<EnemyUnit>): EnemyUnitValidationResult {
    const errors: string[] = [];
    const warnings: string[] = [];

    // Required fields validation
    if (!unit.name || unit.name.trim() === '') {
      errors.push('Unit name is required');
    }

    if (!unit.unit_types || !Array.isArray(unit.unit_types) || unit.unit_types.length === 0) {
      errors.push('At least one unit type is required');
    }

    if (typeof unit.health !== 'number' || unit.health <= 0) {
      errors.push('Health must be a positive number');
    }

    if (typeof unit.strength !== 'number' || unit.strength <= 0) {
      errors.push('Strength must be a positive number');
    }

    // Optional fields validation
    if (unit.attack_modifiers) {
      if (!Array.isArray(unit.attack_modifiers)) {
        errors.push('Attack modifiers must be an array');
      } else {
        unit.attack_modifiers.forEach((modifier, index) => {
          if (!modifier.target_type || modifier.target_type.trim() === '') {
            errors.push(`Attack modifier ${index + 1}: target_type is required`);
          }
          if (modifier.modifier_type !== 'Strength') {
            errors.push(`Attack modifier ${index + 1}: modifier_type must be 'Strength'`);
          }
          if (typeof modifier.value !== 'number' || modifier.value < 0) {
            errors.push(`Attack modifier ${index + 1}: value must be a non-negative number`);
          }
        });
      }
    }

    // Warnings for unusual values
    if (unit.health && unit.health > 10000000) {
      warnings.push('Health value is unusually high');
    }

    if (unit.strength && unit.strength > 5000000) {
      warnings.push('Strength value is unusually high');
    }

    return {
      isValid: errors.length === 0,
      errors,
      warnings: warnings.length > 0 ? warnings : undefined
    };
  }

  /**
   * Create a user enemy unit from base enemy unit data
   */
  static createUserEnemyUnit(baseUnit: EnemyUnit): UserEnemyUnit {
    const now = new Date();
    return {
      ...baseUnit,
      id: `user_enemy_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      createdAt: now,
      modifiedAt: now
    };
  }

  /**
   * Update a user enemy unit's modified timestamp
   */
  static updateUserEnemyUnit(unit: UserEnemyUnit, updates: Partial<EnemyUnit>): UserEnemyUnit {
    return {
      ...unit,
      ...updates,
      modifiedAt: new Date()
    };
  }
}

