/**
 * Enemy Unit Data Loader for the Army Stacking Calculator
 * Loads and manages enemy unit data from JSON files
 */

import { 
  EnemyUnit, 
  EnemyUnitWithCalculatedProps, 
  EnemyUnitSummary, 
  EnemyUnitFilter,
  EnemyUnitUtils 
} from '../types/enemyUnits';

export class EnemyUnitLoader {
  private enemyUnits: EnemyUnit[] = [];
  private enemyUnitsByName: Map<string, EnemyUnit> = new Map();
  private enemyUnitsByType: Map<string, EnemyUnit[]> = new Map();

  constructor() {
    this.resetData();
  }

  /**
   * Load enemy units from a JSON file or URL
   */
  async loadEnemyUnits(source: string | EnemyUnit[]): Promise<EnemyUnit[]> {
    try {
      let enemyUnitsData: EnemyUnit[];

      if (typeof source === 'string') {
        console.log(`Loading enemy units from: ${source}`);
        const response = await fetch(source);
        
        if (!response.ok) {
          throw new Error(`Failed to fetch enemy units: ${response.status} ${response.statusText}`);
        }
        
        enemyUnitsData = await response.json();
      } else {
        enemyUnitsData = source;
      }

      if (!Array.isArray(enemyUnitsData)) {
        throw new Error('Enemy unit data must be an array');
      }

      this.enemyUnits = enemyUnitsData.map(unitData => this.validateAndNormalizeEnemyUnit(unitData));
      this.buildLookups();

      console.log(` Loaded ${this.enemyUnits.length} enemy units successfully`);
      return this.enemyUnits;
    } catch (error) {
      console.error(' Error loading enemy units:', error);
      throw error;
    }
  }

  /**
   * Validate and normalize enemy unit data
   */
  private validateAndNormalizeEnemyUnit(unitData: any): EnemyUnit {
    // Ensure all required fields exist with defaults
    const enemyUnit: EnemyUnit = {
      name: unitData.name || 'Unknown Enemy',
      unit_types: Array.isArray(unitData.unit_types) ? unitData.unit_types : [],
      health: Number(unitData.health) || 0,
      strength: Number(unitData.strength) || 0,
      attack_modifiers: Array.isArray(unitData.attack_modifiers) ? unitData.attack_modifiers : []
    };

    // Validate unit types
    if (!Array.isArray(enemyUnit.unit_types) || enemyUnit.unit_types.length === 0) {
      console.warn(`Enemy unit ${enemyUnit.name} has no unit types, adding 'Unknown'`);
      enemyUnit.unit_types = ['Unknown'];
    }

    // Validate numeric values
    if (enemyUnit.health <= 0) {
      console.warn(`Enemy unit ${enemyUnit.name} has invalid health: ${enemyUnit.health}`);
      enemyUnit.health = 1; // Minimum health
    }

    if (enemyUnit.strength < 0) {
      console.warn(`Enemy unit ${enemyUnit.name} has negative strength: ${enemyUnit.strength}`);
      enemyUnit.strength = 0;
    }

    // Validate attack modifiers
    if (enemyUnit.attack_modifiers && Array.isArray(enemyUnit.attack_modifiers)) {
      enemyUnit.attack_modifiers = enemyUnit.attack_modifiers.filter(modifier => {
        if (!modifier || typeof modifier !== 'object') {
          console.warn(`Enemy unit ${enemyUnit.name} has invalid attack modifier object`);
          return false;
        }
        if (!modifier.target_type || typeof modifier.value !== 'number') {
          console.warn(`Enemy unit ${enemyUnit.name} has invalid attack modifier: ${JSON.stringify(modifier)}`);
          return false;
        }
        return true;
      });
    }

    return enemyUnit;
  }

  /**
   * Build lookup dictionaries for efficient access
   */
  private buildLookups(): void {
    this.resetData();

    this.enemyUnitsByName = new Map(this.enemyUnits.map(unit => [unit.name, unit]));

    // Categorize units by their types
    this.enemyUnits.forEach(unit => {
      unit.unit_types.forEach(type => {
        if (!this.enemyUnitsByType.has(type)) {
          this.enemyUnitsByType.set(type, []);
        }
        this.enemyUnitsByType.get(type)!.push(unit);
      });
    });

    // Sort units within each type by strength
    this.enemyUnitsByType.forEach(units => {
      units.sort((a, b) => b.strength - a.strength); // Descending order for enemies
    });
  }

  /**
   * Reset internal data structures
   */
  private resetData(): void {
    this.enemyUnitsByName.clear();
    this.enemyUnitsByType.clear();
  }

  /**
   * Get all loaded enemy units
   */
  getAllEnemyUnits(): EnemyUnit[] {
    return [...this.enemyUnits];
  }

  /**
   * Get an enemy unit by its name
   */
  getEnemyUnitByName(name: string): EnemyUnit | undefined {
    return this.enemyUnitsByName.get(name);
  }

  /**
   * Get enemy units by unit type
   */
  getEnemyUnitsByType(unitType: string): EnemyUnit[] {
    return [...(this.enemyUnitsByType.get(unitType) || [])];
  }

  /**
   * Get all unique unit types from loaded enemy units
   */
  getUniqueEnemyUnitTypes(): string[] {
    return Array.from(this.enemyUnitsByType.keys()).sort();
  }

  /**
   * Filter enemy units by various criteria
   */
  filterEnemyUnits(filter: EnemyUnitFilter): EnemyUnit[] {
    let filtered = this.enemyUnits;
    
    if (filter.unitTypes && filter.unitTypes.length > 0) {
      filtered = filtered.filter(u => 
        filter.unitTypes!.some(type => u.unit_types.includes(type))
      );
    }
    
    if (filter.minStrength !== undefined) {
      filtered = filtered.filter(u => u.strength >= filter.minStrength!);
    }
    
    if (filter.maxStrength !== undefined) {
      filtered = filtered.filter(u => u.strength <= filter.maxStrength!);
    }

    if (filter.minHealth !== undefined) {
      filtered = filtered.filter(u => u.health >= filter.minHealth!);
    }
    
    if (filter.maxHealth !== undefined) {
      filtered = filtered.filter(u => u.health <= filter.maxHealth!);
    }

    // Note: hasAttackModifiers filter removed as it's not in the EnemyUnitFilter interface
    
    return filtered;
  }

  /**
   * Search enemy units by name (case-insensitive, partial match)
   */
  searchEnemyUnits(query: string): EnemyUnit[] {
    if (!query.trim()) {
      return this.getAllEnemyUnits();
    }

    const lowerQuery = query.toLowerCase();
    return this.enemyUnits.filter(unit => 
      unit.name.toLowerCase().includes(lowerQuery)
    );
  }

  /**
   * Get enemy units with enhanced calculated properties
   */
  getEnhancedEnemyUnits(): EnemyUnitWithCalculatedProps[] {
    return this.enemyUnits.map(unit => ({
      ...unit,
      get strengthPerHealth(): number {
        return EnemyUnitUtils.getStrengthPerHealth(unit);
      },
      get effectivenessScore(): number {
        return EnemyUnitUtils.getEffectivenessScore(unit);
      }
    }));
  }

  /**
   * Get a summary of loaded enemy units
   */
  getEnemyUnitSummary(): EnemyUnitSummary {
    if (this.enemyUnits.length === 0) {
      return {
        totalUnits: 0,
        byUnitType: {},
        strengthRange: { min: 0, max: 0, average: 0 },
        healthRange: { min: 0, max: 0, average: 0 }
      };
    }

    const strengths = this.enemyUnits.map(u => u.strength);
    const healths = this.enemyUnits.map(u => u.health);

    // Count units by type
    const byUnitType: Record<string, number> = {};
    this.enemyUnitsByType.forEach((units, type) => {
      byUnitType[type] = units.length;
    });

    return {
      totalUnits: this.enemyUnits.length,
      byUnitType,
      strengthRange: {
        min: Math.min(...strengths),
        max: Math.max(...strengths),
        average: Math.round(strengths.reduce((a, b) => a + b, 0) / strengths.length)
      },
      healthRange: {
        min: Math.min(...healths),
        max: Math.max(...healths),
        average: Math.round(healths.reduce((a, b) => a + b, 0) / healths.length)
      }
    };
  }

  /**
   * Get statistics about the loaded enemy units
   */
  getStatistics() {
    if (this.enemyUnits.length === 0) {
      return {
        totalUnits: 0,
        unitTypeDistribution: {},
        strengthStats: { min: 0, max: 0, avg: 0 },
        healthStats: { min: 0, max: 0, avg: 0 },
        topUnitsByStrength: [],
        topUnitsByHealth: [],
        unitsWithAttackModifiers: 0
      };
    }

    const strengths = this.enemyUnits.map(u => u.strength);
    const healths = this.enemyUnits.map(u => u.health);

    // Count units by type
    const unitTypeDistribution: Record<string, number> = {};
    this.enemyUnitsByType.forEach((units, type) => {
      unitTypeDistribution[type] = units.length;
    });

    // Count units with attack modifiers
    const unitsWithAttackModifiers = this.enemyUnits.filter(u =>
      u.attack_modifiers && Array.isArray(u.attack_modifiers) && u.attack_modifiers.length > 0
    ).length;

    return {
      totalUnits: this.enemyUnits.length,
      unitTypeDistribution,
      strengthStats: {
        min: Math.min(...strengths),
        max: Math.max(...strengths),
        avg: Math.round(strengths.reduce((a, b) => a + b, 0) / strengths.length)
      },
      healthStats: {
        min: Math.min(...healths),
        max: Math.max(...healths),
        avg: Math.round(healths.reduce((a, b) => a + b, 0) / healths.length)
      },
      topUnitsByStrength: [...this.enemyUnits]
        .sort((a, b) => b.strength - a.strength)
        .slice(0, 10),
      topUnitsByHealth: [...this.enemyUnits]
        .sort((a, b) => b.health - a.health)
        .slice(0, 10),
      unitsWithAttackModifiers
    };
  }

  /**
   * Get enemy units that have attack modifiers against specific unit types
   */
  getEnemyUnitsWithModifiersAgainst(unitTypes: string[]): EnemyUnit[] {
    return this.enemyUnits.filter(unit => {
      if (!unit.attack_modifiers || !Array.isArray(unit.attack_modifiers)) return false;
      return unitTypes.some(type =>
        unit.attack_modifiers!.some(modifier =>
          modifier.target_type.toLowerCase() === type.toLowerCase()
        )
      );
    });
  }

  /**
   * Get the most effective enemy units against specific player unit types
   */
  getMostEffectiveAgainst(playerUnitTypes: string[], limit: number = 5): EnemyUnit[] {
    return this.enemyUnits
      .map(unit => ({
        unit,
        effectiveness: this.calculateEffectivenessAgainst(unit, playerUnitTypes)
      }))
      .sort((a, b) => b.effectiveness - a.effectiveness)
      .slice(0, limit)
      .map(item => item.unit);
  }

  /**
   * Calculate effectiveness of an enemy unit against specific player unit types
   */
  private calculateEffectivenessAgainst(unit: EnemyUnit, playerUnitTypes: string[]): number {
    let totalEffectiveness = EnemyUnitUtils.getEffectivenessScore(unit);
    
    if (unit.attack_modifiers && Array.isArray(unit.attack_modifiers)) {
      playerUnitTypes.forEach(playerType => {
        const modifier = EnemyUnitUtils.getAttackModifierAgainst(unit, playerType);
        totalEffectiveness += modifier * 0.1; // Weight modifiers at 10% of base effectiveness
      });
    }
    
    return totalEffectiveness;
  }
}