/**
 * Enemy Unit Validation Service for the Army Stacking Calculator
 * Provides comprehensive validation for enemy unit data
 */

import { 
  EnemyUnit, 
  UserEnemyUnit, 
  EnemyUnitValidationResult,
  EnemyUnitUtils,
  ENEMY_UNIT_TYPES,
  ENEMY_TARGET_TYPES,
  ENEMY_ATTACK_MODIFIER_TYPES
} from '../types/enemyUnits';

export class EnemyUnitValidationService {
  
  /**
   * Validate a complete enemy unit
   */
  static validateEnemyUnit(unit: Partial<EnemyUnit>): EnemyUnitValidationResult {
    return EnemyUnitUtils.validateEnemyUnit(unit);
  }

  /**
   * Validate a user-created enemy unit
   */
  static validateUserEnemyUnit(unit: Partial<UserEnemyUnit>): EnemyUnitValidationResult {
    const baseValidation = EnemyUnitUtils.validateEnemyUnit(unit);
    const errors = [...baseValidation.errors];
    const warnings = [...(baseValidation.warnings || [])];

    // Additional validation for user units
    if (!unit.id || typeof unit.id !== 'string' || unit.id.trim() === '') {
      errors.push('User enemy unit must have a valid ID');
    }

    if (!unit.createdAt || !(unit.createdAt instanceof Date)) {
      errors.push('User enemy unit must have a valid creation date');
    }

    if (!unit.modifiedAt || !(unit.modifiedAt instanceof Date)) {
      errors.push('User enemy unit must have a valid modification date');
    }

    // Check if modification date is after creation date
    if (unit.createdAt && unit.modifiedAt && 
        unit.createdAt instanceof Date && unit.modifiedAt instanceof Date) {
      if (unit.modifiedAt < unit.createdAt) {
        errors.push('Modification date cannot be before creation date');
      }
    }

    return {
      isValid: errors.length === 0,
      errors,
      warnings: warnings.length > 0 ? warnings : undefined
    };
  }

  /**
   * Validate enemy unit name
   */
  static validateName(name: string): { isValid: boolean; error?: string } {
    if (!name || typeof name !== 'string') {
      return { isValid: false, error: 'Name must be a non-empty string' };
    }

    const trimmedName = name.trim();
    if (trimmedName.length === 0) {
      return { isValid: false, error: 'Name cannot be empty or only whitespace' };
    }

    if (trimmedName.length > 100) {
      return { isValid: false, error: 'Name cannot exceed 100 characters' };
    }

    // Check for invalid characters
    const invalidChars = /[<>\"'&]/;
    if (invalidChars.test(trimmedName)) {
      return { isValid: false, error: 'Name contains invalid characters (<, >, ", \', &)' };
    }

    return { isValid: true };
  }

  /**
   * Validate unit types array
   */
  static validateUnitTypes(unitTypes: any): { isValid: boolean; error?: string; warnings?: string[] } {
    const warnings: string[] = [];

    if (!Array.isArray(unitTypes)) {
      return { isValid: false, error: 'Unit types must be an array' };
    }

    if (unitTypes.length === 0) {
      return { isValid: false, error: 'At least one unit type is required' };
    }

    if (unitTypes.length > 10) {
      return { isValid: false, error: 'Cannot have more than 10 unit types' };
    }

    // Validate each unit type
    for (let i = 0; i < unitTypes.length; i++) {
      const type = unitTypes[i];
      if (typeof type !== 'string' || type.trim() === '') {
        return { isValid: false, error: `Unit type ${i + 1} must be a non-empty string` };
      }

      // Check if it's a known unit type
      if (!ENEMY_UNIT_TYPES.includes(type as any)) {
        warnings.push(`Unit type '${type}' is not a standard type`);
      }
    }

    // Check for duplicates
    const uniqueTypes = new Set(unitTypes);
    if (uniqueTypes.size !== unitTypes.length) {
      return { isValid: false, error: 'Unit types cannot contain duplicates' };
    }

    return { 
      isValid: true, 
      warnings: warnings.length > 0 ? warnings : undefined 
    };
  }

  /**
   * Validate health value
   */
  static validateHealth(health: any): { isValid: boolean; error?: string; warnings?: string[] } {
    const warnings: string[] = [];

    if (typeof health !== 'number') {
      return { isValid: false, error: 'Health must be a number' };
    }

    if (!Number.isFinite(health)) {
      return { isValid: false, error: 'Health must be a finite number' };
    }

    if (health <= 0) {
      return { isValid: false, error: 'Health must be greater than 0' };
    }

    if (health > 50000000) {
      return { isValid: false, error: 'Health cannot exceed 50,000,000' };
    }

    // Warnings for unusual values
    if (health < 100) {
      warnings.push('Health value is unusually low (less than 100)');
    }

    if (health > 10000000) {
      warnings.push('Health value is unusually high (greater than 10,000,000)');
    }

    return { 
      isValid: true, 
      warnings: warnings.length > 0 ? warnings : undefined 
    };
  }

  /**
   * Validate strength value
   */
  static validateStrength(strength: any): { isValid: boolean; error?: string; warnings?: string[] } {
    const warnings: string[] = [];

    if (typeof strength !== 'number') {
      return { isValid: false, error: 'Strength must be a number' };
    }

    if (!Number.isFinite(strength)) {
      return { isValid: false, error: 'Strength must be a finite number' };
    }

    if (strength < 0) {
      return { isValid: false, error: 'Strength cannot be negative' };
    }

    if (strength > 25000000) {
      return { isValid: false, error: 'Strength cannot exceed 25,000,000' };
    }

    // Warnings for unusual values
    if (strength === 0) {
      warnings.push('Strength value of 0 means this unit cannot deal damage');
    }

    if (strength < 50) {
      warnings.push('Strength value is unusually low (less than 50)');
    }

    if (strength > 5000000) {
      warnings.push('Strength value is unusually high (greater than 5,000,000)');
    }

    return { 
      isValid: true, 
      warnings: warnings.length > 0 ? warnings : undefined 
    };
  }

  /**
   * Validate attack modifiers array
   */
  static validateAttackModifiers(attackModifiers: any): { isValid: boolean; error?: string; warnings?: string[] } {
    const warnings: string[] = [];

    // Attack modifiers are optional
    if (attackModifiers === undefined || attackModifiers === null) {
      return { isValid: true };
    }

    if (!Array.isArray(attackModifiers)) {
      return { isValid: false, error: 'Attack modifiers must be an array' };
    }

    if (attackModifiers.length > 20) {
      return { isValid: false, error: 'Cannot have more than 20 attack modifiers' };
    }

    // Validate each modifier
    for (let i = 0; i < attackModifiers.length; i++) {
      const modifier = attackModifiers[i];
      
      if (!modifier || typeof modifier !== 'object') {
        return { isValid: false, error: `Attack modifier ${i + 1} must be an object` };
      }

      // Validate target_type
      if (!modifier.target_type || typeof modifier.target_type !== 'string') {
        return { isValid: false, error: `Attack modifier ${i + 1}: target_type is required and must be a string` };
      }

      if (!ENEMY_TARGET_TYPES.includes(modifier.target_type as any)) {
        warnings.push(`Attack modifier ${i + 1}: '${modifier.target_type}' is not a standard target type`);
      }

      // Validate modifier_type
      if (!modifier.modifier_type || !ENEMY_ATTACK_MODIFIER_TYPES.includes(modifier.modifier_type as any)) {
        return { isValid: false, error: `Attack modifier ${i + 1}: modifier_type must be 'Strength'` };
      }

      // Validate value
      if (typeof modifier.value !== 'number' || !Number.isFinite(modifier.value)) {
        return { isValid: false, error: `Attack modifier ${i + 1}: value must be a finite number` };
      }

      if (modifier.value < 0) {
        return { isValid: false, error: `Attack modifier ${i + 1}: value cannot be negative` };
      }

      if (modifier.value > 10000000) {
        return { isValid: false, error: `Attack modifier ${i + 1}: value cannot exceed 10,000,000` };
      }

      // Warning for very high modifier values
      if (modifier.value > 1000000) {
        warnings.push(`Attack modifier ${i + 1}: value is unusually high (${modifier.value})`);
      }
    }

    // Check for duplicate target types
    const targetTypes = attackModifiers.map(mod => mod.target_type?.toLowerCase()).filter(Boolean);
    const uniqueTargetTypes = new Set(targetTypes);
    if (uniqueTargetTypes.size !== targetTypes.length) {
      return { isValid: false, error: 'Attack modifiers cannot have duplicate target types' };
    }

    return { 
      isValid: true, 
      warnings: warnings.length > 0 ? warnings : undefined 
    };
  }

  /**
   * Validate enemy unit data for import/export
   */
  static validateForImport(data: any): EnemyUnitValidationResult {
    const errors: string[] = [];
    const warnings: string[] = [];

    if (!data || typeof data !== 'object') {
      return {
        isValid: false,
        errors: ['Data must be an object']
      };
    }

    // Validate each field individually and collect all errors/warnings
    const nameValidation = this.validateName(data.name);
    if (!nameValidation.isValid) {
      errors.push(nameValidation.error!);
    }

    const unitTypesValidation = this.validateUnitTypes(data.unit_types);
    if (!unitTypesValidation.isValid) {
      errors.push(unitTypesValidation.error!);
    } else if (unitTypesValidation.warnings) {
      warnings.push(...unitTypesValidation.warnings);
    }

    const healthValidation = this.validateHealth(data.health);
    if (!healthValidation.isValid) {
      errors.push(healthValidation.error!);
    } else if (healthValidation.warnings) {
      warnings.push(...healthValidation.warnings);
    }

    const strengthValidation = this.validateStrength(data.strength);
    if (!strengthValidation.isValid) {
      errors.push(strengthValidation.error!);
    } else if (strengthValidation.warnings) {
      warnings.push(...strengthValidation.warnings);
    }

    const attackModifiersValidation = this.validateAttackModifiers(data.attack_modifiers);
    if (!attackModifiersValidation.isValid) {
      errors.push(attackModifiersValidation.error!);
    } else if (attackModifiersValidation.warnings) {
      warnings.push(...attackModifiersValidation.warnings);
    }

    return {
      isValid: errors.length === 0,
      errors,
      warnings: warnings.length > 0 ? warnings : undefined
    };
  }

  /**
   * Validate multiple enemy units (for batch operations)
   */
  static validateMultipleUnits(units: any[]): { 
    isValid: boolean; 
    results: Array<{ index: number; validation: EnemyUnitValidationResult }>;
    summary: { total: number; valid: number; invalid: number; warnings: number };
  } {
    if (!Array.isArray(units)) {
      return {
        isValid: false,
        results: [],
        summary: { total: 0, valid: 0, invalid: 0, warnings: 0 }
      };
    }

    const results = units.map((unit, index) => ({
      index,
      validation: this.validateForImport(unit)
    }));

    const summary = {
      total: units.length,
      valid: results.filter(r => r.validation.isValid).length,
      invalid: results.filter(r => !r.validation.isValid).length,
      warnings: results.filter(r => r.validation.warnings && r.validation.warnings.length > 0).length
    };

    return {
      isValid: summary.invalid === 0,
      results,
      summary
    };
  }

  /**
   * Sanitize enemy unit data by removing/fixing invalid values
   */
  static sanitizeEnemyUnit(data: any): EnemyUnit {
    const sanitized: EnemyUnit = {
      name: typeof data.name === 'string' ? data.name.trim() : 'Unknown Enemy',
      unit_types: Array.isArray(data.unit_types) ? 
        data.unit_types.filter((type: any) => typeof type === 'string' && type.trim() !== '') :
        ['Epic Monster'],
      health: typeof data.health === 'number' && data.health > 0 ? Math.min(data.health, 50000000) : 10000,
      strength: typeof data.strength === 'number' && data.strength >= 0 ? Math.min(data.strength, 25000000) : 5000,
      attack_modifiers: Array.isArray(data.attack_modifiers) ? 
        data.attack_modifiers
          .filter((mod: any) => mod && typeof mod === 'object' &&
                        typeof mod.target_type === 'string' &&
                        typeof mod.value === 'number' &&
                        mod.value >= 0)
          .map((mod: any) => ({
            target_type: mod.target_type,
            modifier_type: 'Strength' as const,
            value: Math.min(mod.value, 10000000)
          })) :
        []
    };

    // Ensure name is not empty
    if (sanitized.name.length === 0) {
      sanitized.name = 'Unknown Enemy';
    }

    // Ensure at least one unit type
    if (sanitized.unit_types.length === 0) {
      sanitized.unit_types = ['Epic Monster'];
    }

    return sanitized;
  }
}