  /**
   * Display pre-calculated battle results without running a new simulation
   */
  displayPreCalculatedResults(battleAnalysis: any): void {
    console.log('BattleSimulationComponent: displayPreCalculatedResults called', battleAnalysis);
    if (!this.container) {
      console.log('BattleSimulationComponent: no container');
      return;
    }

    // Store the analysis
    this.currentAnalysis = battleAnalysis;

    // Find the results container
    const resultsContainer = this.container.querySelector('#simulation-results');
    if (!resultsContainer) {
      console.log('BattleSimulationComponent: simulation-results container not found');
      return;
    }
    console.log('BattleSimulationComponent: found results container', resultsContainer);

    // Show the results container
    resultsContainer.classList.remove('hidden');

    // Display the pre-calculated results
    this.displayResults();

    // Make sure results are visible
    this.showResults(true);

    // Hide simulation controls for damage optimization mode (view-only)
    const controlsContainer = this.container.querySelector('.simulation-controls');
    const enemyInputContainer = this.container.querySelector('.enemy-input-container');
    if (controlsContainer) {
      (controlsContainer as HTMLElement).style.display = 'none';
    }
    if (enemyInputContainer) {
      (enemyInputContainer as HTMLElement).style.display = 'none';
    }

    // Show a note that these are pre-calculated results
    const noteContainer = this.container.querySelector('#simulation-note');
    if (noteContainer) {
      noteContainer.innerHTML = `
        <div class="info-note">
          <span class="note-icon"></span>
          <span class="note-text">Showing pre-calculated battle results from damage optimization</span>
        </div>
      `;
      noteContainer.classList.remove('hidden');
    }
  }

  /**
   * Show simulation controls (for stacking mode)
   */
  private showSimulationControls(): void {
    if (!this.container) return;

    const controlsContainer = this.container.querySelector('.simulation-controls');
    const enemyInputContainer = this.container.querySelector('.enemy-input-container');
    if (controlsContainer) {
      (controlsContainer as HTMLElement).style.display = 'block';
    }
    if (enemyInputContainer) {
      (enemyInputContainer as HTMLElement).style.display = 'block';
    }
  }
