/**
 * Enemy Unit Storage Service for the Army Stacking Calculator
 * Manages user-created enemy units in browser memory (localStorage)
 */

import { 
  EnemyUnit, 
  UserEnemyUnit, 
  EnemyUnitUtils,
  EnemyUnitValidationResult
} from '../types/enemyUnits';
import { EnemyUnitValidationService } from './enemyUnitValidation';

export interface EnemyUnitStorageOptions {
  /** Maximum number of user enemy units to store */
  maxUnits?: number;
  /** Storage key prefix for localStorage */
  storagePrefix?: string;
  /** Whether to validate units on load */
  validateOnLoad?: boolean;
}

export interface EnemyUnitStorageStats {
  /** Total number of stored units */
  totalUnits: number;
  /** Storage size in bytes (approximate) */
  storageSize: number;
  /** Last modification timestamp */
  lastModified: Date | null;
  /** Number of units created today */
  unitsCreatedToday: number;
}

export class EnemyUnitStorageService {
  private static readonly DEFAULT_STORAGE_KEY = 'army_calculator_user_enemy_units';
  private static readonly METADATA_KEY = 'army_calculator_enemy_units_metadata';
  private static readonly DEFAULT_MAX_UNITS = 100;

  private storageKey: string;
  private metadataKey: string;
  private maxUnits: number;
  private validateOnLoad: boolean;

  constructor(options: EnemyUnitStorageOptions = {}) {
    this.storageKey = options.storagePrefix ? 
      `${options.storagePrefix}_user_enemy_units` : 
      EnemyUnitStorageService.DEFAULT_STORAGE_KEY;
    
    this.metadataKey = options.storagePrefix ? 
      `${options.storagePrefix}_enemy_units_metadata` : 
      EnemyUnitStorageService.METADATA_KEY;
    
    this.maxUnits = options.maxUnits || EnemyUnitStorageService.DEFAULT_MAX_UNITS;
    this.validateOnLoad = options.validateOnLoad !== false; // Default to true
  }

  /**
   * Check if localStorage is available
   */
  private isStorageAvailable(): boolean {
    try {
      const test = '__storage_test__';
      localStorage.setItem(test, test);
      localStorage.removeItem(test);
      return true;
    } catch {
      return false;
    }
  }

  /**
   * Get all stored user enemy units
   */
  getAllUserEnemyUnits(): UserEnemyUnit[] {
    if (!this.isStorageAvailable()) {
      console.warn('localStorage is not available');
      return [];
    }

    try {
      const stored = localStorage.getItem(this.storageKey);
      if (!stored) {
        return [];
      }

      const units: UserEnemyUnit[] = JSON.parse(stored);
      
      // Convert date strings back to Date objects
      const processedUnits = units.map(unit => ({
        ...unit,
        createdAt: new Date(unit.createdAt),
        modifiedAt: new Date(unit.modifiedAt)
      }));

      // Validate units if enabled
      if (this.validateOnLoad) {
        return processedUnits.filter(unit => {
          const validation = EnemyUnitValidationService.validateUserEnemyUnit(unit);
          if (!validation.isValid) {
            console.warn(`Invalid stored enemy unit removed: ${unit.name}`, validation.errors);
            return false;
          }
          return true;
        });
      }

      return processedUnits;
    } catch (error) {
      console.error('Error loading user enemy units from storage:', error);
      return [];
    }
  }

  /**
   * Save all user enemy units to storage
   */
  private saveAllUserEnemyUnits(units: UserEnemyUnit[]): boolean {
    if (!this.isStorageAvailable()) {
      console.warn('localStorage is not available');
      return false;
    }

    try {
      localStorage.setItem(this.storageKey, JSON.stringify(units));
      this.updateMetadata();
      return true;
    } catch (error) {
      console.error('Error saving user enemy units to storage:', error);
      return false;
    }
  }

  /**
   * Add a new user enemy unit
   */
  addUserEnemyUnit(enemyUnit: EnemyUnit): { success: boolean; unit?: UserEnemyUnit; error?: string } {
    // Validate the enemy unit
    const validation = EnemyUnitValidationService.validateEnemyUnit(enemyUnit);
    if (!validation.isValid) {
      return {
        success: false,
        error: `Validation failed: ${validation.errors.join(', ')}`
      };
    }

    const units = this.getAllUserEnemyUnits();

    // Check if we've reached the maximum number of units
    if (units.length >= this.maxUnits) {
      return {
        success: false,
        error: `Maximum number of units (${this.maxUnits}) reached`
      };
    }

    // Check for duplicate names
    if (units.some(unit => unit.name.toLowerCase() === enemyUnit.name.toLowerCase())) {
      return {
        success: false,
        error: `A unit with the name "${enemyUnit.name}" already exists`
      };
    }

    // Create user enemy unit
    const userUnit = EnemyUnitUtils.createUserEnemyUnit(enemyUnit);
    units.push(userUnit);

    const saved = this.saveAllUserEnemyUnits(units);
    if (!saved) {
      return {
        success: false,
        error: 'Failed to save unit to storage'
      };
    }

    return {
      success: true,
      unit: userUnit
    };
  }

  /**
   * Update an existing user enemy unit
   */
  updateUserEnemyUnit(id: string, updates: Partial<EnemyUnit>): { success: boolean; unit?: UserEnemyUnit; error?: string } {
    const units = this.getAllUserEnemyUnits();
    const unitIndex = units.findIndex(unit => unit.id === id);

    if (unitIndex === -1) {
      return {
        success: false,
        error: 'Unit not found'
      };
    }

    const existingUnit = units[unitIndex];
    const updatedUnit = { ...existingUnit, ...updates };

    // Validate the updated unit
    const validation = EnemyUnitValidationService.validateUserEnemyUnit(updatedUnit);
    if (!validation.isValid) {
      return {
        success: false,
        error: `Validation failed: ${validation.errors.join(', ')}`
      };
    }

    // Check for duplicate names (excluding current unit)
    if (updates.name && 
        units.some((unit, index) => 
          index !== unitIndex && 
          unit.name.toLowerCase() === updates.name!.toLowerCase()
        )) {
      return {
        success: false,
        error: `A unit with the name "${updates.name}" already exists`
      };
    }

    // Update the unit
    const finalUnit = EnemyUnitUtils.updateUserEnemyUnit(existingUnit, updates);
    units[unitIndex] = finalUnit;

    const saved = this.saveAllUserEnemyUnits(units);
    if (!saved) {
      return {
        success: false,
        error: 'Failed to save updated unit to storage'
      };
    }

    return {
      success: true,
      unit: finalUnit
    };
  }

  /**
   * Delete a user enemy unit
   */
  deleteUserEnemyUnit(id: string): { success: boolean; error?: string } {
    const units = this.getAllUserEnemyUnits();
    const unitIndex = units.findIndex(unit => unit.id === id);

    if (unitIndex === -1) {
      return {
        success: false,
        error: 'Unit not found'
      };
    }

    units.splice(unitIndex, 1);

    const saved = this.saveAllUserEnemyUnits(units);
    if (!saved) {
      return {
        success: false,
        error: 'Failed to save changes to storage'
      };
    }

    return { success: true };
  }

  /**
   * Get a user enemy unit by ID
   */
  getUserEnemyUnitById(id: string): UserEnemyUnit | null {
    const units = this.getAllUserEnemyUnits();
    return units.find(unit => unit.id === id) || null;
  }

  /**
   * Search user enemy units by name
   */
  searchUserEnemyUnits(query: string): UserEnemyUnit[] {
    const units = this.getAllUserEnemyUnits();
    if (!query.trim()) {
      return units;
    }

    const lowerQuery = query.toLowerCase();
    return units.filter(unit => 
      unit.name.toLowerCase().includes(lowerQuery)
    );
  }

  /**
   * Clear all user enemy units
   */
  clearAllUserEnemyUnits(): { success: boolean; error?: string } {
    if (!this.isStorageAvailable()) {
      return {
        success: false,
        error: 'localStorage is not available'
      };
    }

    try {
      localStorage.removeItem(this.storageKey);
      localStorage.removeItem(this.metadataKey);
      return { success: true };
    } catch (error) {
      return {
        success: false,
        error: 'Failed to clear storage'
      };
    }
  }

  /**
   * Export user enemy units as JSON
   */
  exportUserEnemyUnits(): { success: boolean; data?: string; error?: string } {
    try {
      const units = this.getAllUserEnemyUnits();
      const exportData = {
        version: '1.0',
        exportDate: new Date().toISOString(),
        units: units.map(unit => ({
          name: unit.name,
          unit_types: unit.unit_types,
          health: unit.health,
          strength: unit.strength,
          attack_modifiers: unit.attack_modifiers,
          createdAt: unit.createdAt.toISOString(),
          modifiedAt: unit.modifiedAt.toISOString()
        }))
      };

      return {
        success: true,
        data: JSON.stringify(exportData, null, 2)
      };
    } catch (error) {
      return {
        success: false,
        error: 'Failed to export units'
      };
    }
  }

  /**
   * Import user enemy units from JSON
   */
  importUserEnemyUnits(jsonData: string, options: { replace?: boolean; skipDuplicates?: boolean } = {}): 
    { success: boolean; imported?: number; skipped?: number; errors?: string[] } {
    
    try {
      const importData = JSON.parse(jsonData);
      
      if (!importData.units || !Array.isArray(importData.units)) {
        return {
          success: false,
          errors: ['Invalid import format: units array not found']
        };
      }

      const existingUnits = options.replace ? [] : this.getAllUserEnemyUnits();
      const errors: string[] = [];
      let imported = 0;
      let skipped = 0;

      for (const unitData of importData.units) {
        // Validate unit data
        const validation = EnemyUnitValidationService.validateForImport(unitData);
        if (!validation.isValid) {
          errors.push(`Unit "${unitData.name || 'Unknown'}": ${validation.errors.join(', ')}`);
          skipped++;
          continue;
        }

        // Check for duplicates
        const isDuplicate = existingUnits.some(existing => 
          existing.name.toLowerCase() === unitData.name.toLowerCase()
        );

        if (isDuplicate) {
          if (options.skipDuplicates) {
            skipped++;
            continue;
          } else {
            errors.push(`Unit "${unitData.name}" already exists`);
            skipped++;
            continue;
          }
        }

        // Check unit limit
        if (existingUnits.length >= this.maxUnits) {
          errors.push(`Maximum number of units (${this.maxUnits}) reached`);
          break;
        }

        // Create user unit
        const userUnit = EnemyUnitUtils.createUserEnemyUnit({
          name: unitData.name,
          unit_types: unitData.unit_types,
          health: unitData.health,
          strength: unitData.strength,
          attack_modifiers: unitData.attack_modifiers || []
        });

        existingUnits.push(userUnit);
        imported++;
      }

      // Save all units
      const saved = this.saveAllUserEnemyUnits(existingUnits);
      if (!saved) {
        return {
          success: false,
          errors: ['Failed to save imported units to storage']
        };
      }

      return {
        success: true,
        imported,
        skipped,
        errors: errors.length > 0 ? errors : undefined
      };

    } catch (error) {
      return {
        success: false,
        errors: ['Invalid JSON format']
      };
    }
  }

  /**
   * Get storage statistics
   */
  getStorageStats(): EnemyUnitStorageStats {
    const units = this.getAllUserEnemyUnits();
    const today = new Date();
    today.setHours(0, 0, 0, 0);

    const unitsCreatedToday = units.filter(unit => 
      unit.createdAt >= today
    ).length;

    let lastModified: Date | null = null;
    if (units.length > 0) {
      lastModified = new Date(Math.max(...units.map(unit => unit.modifiedAt.getTime())));
    }

    // Approximate storage size
    const storageSize = this.isStorageAvailable() ? 
      (localStorage.getItem(this.storageKey)?.length || 0) * 2 : 0; // Rough estimate in bytes

    return {
      totalUnits: units.length,
      storageSize,
      lastModified,
      unitsCreatedToday
    };
  }

  /**
   * Update metadata
   */
  private updateMetadata(): void {
    if (!this.isStorageAvailable()) return;

    try {
      const metadata = {
        lastModified: new Date().toISOString(),
        version: '1.0'
      };
      localStorage.setItem(this.metadataKey, JSON.stringify(metadata));
    } catch (error) {
      console.warn('Failed to update metadata:', error);
    }
  }

  /**
   * Get available storage space (approximate)
   */
  getAvailableSpace(): number {
    return Math.max(0, this.maxUnits - this.getAllUserEnemyUnits().length);
  }

  /**
   * Check if storage is near capacity
   */
  isNearCapacity(threshold: number = 0.9): boolean {
    const units = this.getAllUserEnemyUnits();
    return units.length >= (this.maxUnits * threshold);
  }
}