/**
 * Enhanced Battle Simulation Service with Enemy Units Support
 * Extends the base battle simulation to work with specific enemy units
 * while maintaining backward compatibility with abstract enemy groups
 */

import {
  IBattleSimulationService,
  BattleConfiguration,
  BattleResult,
  BattleAnalysis,
  BattleState,
  CombatAction,
  BattleUtils,
  BATTLE_CONSTANTS
} from '../types/battleSimulation';
import { ArmyComposition, StackingGroup } from '../types/units';
import { EnemyUnit, EnemyUnitUtils } from '../types/enemyUnits';
import { BattleSimulationService } from './battleSimulation';

/**
 * Enhanced battle configuration that supports specific enemy units
 */
export interface EnemyUnitBattleConfiguration extends Omit<BattleConfiguration, 'enemyGroupCount'> {
  /** Specific enemy units to fight against */
  enemyUnits: EnemyUnit[];
  /** Whether to use enemy attack modifiers in calculations */
  useEnemyModifiers?: boolean;
}

/**
 * Enhanced battle state that tracks specific enemy units
 */
export interface EnemyUnitBattleState extends Omit<BattleState, 'enemyGroupCount'> {
  /** Current enemy units with their health status */
  enemyUnits: Array<{
    unit: EnemyUnit;
    currentHealth: number;
    isAlive: boolean;
    damageReceived: number;
  }>;
  /** Whether to apply enemy attack modifiers */
  useEnemyModifiers: boolean;
}

/**
 * Enhanced combat action that includes enemy unit details
 */
export interface EnemyUnitCombatAction extends CombatAction {
  /** Enemy unit involved in the action (if applicable) */
  enemyUnit?: EnemyUnit;
  /** Effective strength used in the attack (including modifiers) */
  effectiveStrength?: number;
  /** Attack modifiers applied */
  modifiersApplied?: Array<{ type: string; value: number }>;
}

export class EnemyUnitBattleSimulationService extends BattleSimulationService {
  
  /**
   * Simulate battle with specific enemy units
   */
  simulateBattleWithEnemyUnits(config: EnemyUnitBattleConfiguration): BattleResult {
    // Validate configuration
    this.validateEnemyUnitBattleConfiguration(config);

    // Initialize enhanced battle state
    const state: EnemyUnitBattleState = {
      currentTurn: 0,
      playerGroups: [...config.playerArmy.stackingOrder],
      enemyUnits: config.enemyUnits.map(unit => ({
        unit,
        currentHealth: unit.health,
        isAlive: true,
        damageReceived: 0
      })),
      totalDamageDealt: 0,
      battleEnded: false,
      combatLog: [],
      useEnemyModifiers: config.useEnemyModifiers !== false
    };

    // Main battle loop
    let consecutiveNoActionTurns = 0;
    const maxNoActionTurns = 5;

    while (!this.shouldEnemyUnitBattleEnd(state) && state.currentTurn < BATTLE_CONSTANTS.MAX_BATTLE_TURNS) {
      const previousLogLength = state.combatLog.length;
      state.currentTurn++;

      try {
        this.processEnemyUnitTurn(state, config.playerGoesFirst);
      } catch (error) {
        throw new Error(`Enemy unit battle processing failed on turn ${state.currentTurn}: ${error instanceof Error ? error.message : 'Unknown error'}`);
      }

      // Safety check for progress
      if (state.combatLog.length === previousLogLength) {
        consecutiveNoActionTurns++;
        if (consecutiveNoActionTurns >= maxNoActionTurns) {
          throw new Error(`Battle stalled: No actions taken for ${maxNoActionTurns} consecutive turns`);
        }
      } else {
        consecutiveNoActionTurns = 0;
      }

      // Additional safety check
      if (state.currentTurn > 1 && state.playerGroups.length === 0 && !state.battleEnded) {
        state.battleEnded = true;
        break;
      }
    }

    // Check for timeout
    if (state.currentTurn >= BATTLE_CONSTANTS.MAX_BATTLE_TURNS) {
      throw new Error(`Battle exceeded maximum duration of ${BATTLE_CONSTANTS.MAX_BATTLE_TURNS} turns`);
    }

    // Calculate statistics
    const statistics = BattleUtils.calculateBattleStatistics(state.combatLog, state.totalDamageDealt);

    // Convert back to standard battle configuration for result
    const standardConfig: BattleConfiguration = {
      playerArmy: config.playerArmy,
      enemyGroupCount: config.enemyUnits.length,
      playerGoesFirst: config.playerGoesFirst
    };

    return {
      outcome: 'player_eliminated',
      combatLog: state.combatLog,
      totalDamageDealtToEnemies: state.totalDamageDealt,
      battleDuration: state.currentTurn,
      playerSurvivalTurns: state.currentTurn,
      scenario: config.playerGoesFirst ? 'best_case' : 'worst_case',
      configuration: standardConfig,
      statistics
    };
  }

  /**
   * Simulate both scenarios with enemy units
   */
  simulateBothScenariosWithEnemyUnits(playerArmy: ArmyComposition, enemyUnits: EnemyUnit[], useEnemyModifiers: boolean = true): BattleAnalysis {
    // Validate inputs
    if (!playerArmy) {
      throw new Error('Player army is required');
    }

    if (!enemyUnits || enemyUnits.length === 0) {
      throw new Error('At least one enemy unit is required');
    }

    if (enemyUnits.length > 100) {
      throw new Error('Cannot simulate with more than 100 enemy units (performance limit)');
    }

    // Best case: player goes first
    const bestCaseConfig: EnemyUnitBattleConfiguration = {
      playerArmy,
      enemyUnits,
      playerGoesFirst: true,
      useEnemyModifiers
    };
    const bestCase = this.simulateBattleWithEnemyUnits(bestCaseConfig);

    // Worst case: enemy goes first
    const worstCaseConfig: EnemyUnitBattleConfiguration = {
      playerArmy,
      enemyUnits,
      playerGoesFirst: false,
      useEnemyModifiers
    };
    const worstCase = this.simulateBattleWithEnemyUnits(worstCaseConfig);

    // Calculate comparison metrics
    const comparison = {
      damageDifference: bestCase.totalDamageDealtToEnemies - worstCase.totalDamageDealtToEnemies,
      survivalDifference: bestCase.playerSurvivalTurns - worstCase.playerSurvivalTurns,
      averageDamage: (bestCase.totalDamageDealtToEnemies + worstCase.totalDamageDealtToEnemies) / 2,
      averageSurvival: (bestCase.playerSurvivalTurns + worstCase.playerSurvivalTurns) / 2
    };

    return {
      bestCase,
      worstCase,
      comparison
    };
  }

  /**
   * Validate enemy unit battle configuration
   */
  private validateEnemyUnitBattleConfiguration(config: EnemyUnitBattleConfiguration): void {
    if (!config) {
      throw new Error('Battle configuration is required');
    }

    if (!config.playerArmy) {
      throw new Error('Player army is required');
    }

    if (!config.playerArmy.stackingOrder || config.playerArmy.stackingOrder.length === 0) {
      throw new Error('Player army must have at least one unit group');
    }

    if (!config.enemyUnits || !Array.isArray(config.enemyUnits) || config.enemyUnits.length === 0) {
      throw new Error('At least one enemy unit is required');
    }

    if (config.enemyUnits.length > 100) {
      throw new Error('Cannot have more than 100 enemy units (performance limit)');
    }

    // Validate each player unit group
    for (const group of config.playerArmy.stackingOrder) {
      if (!group.unitName || group.unitName.trim() === '') {
        throw new Error('All unit groups must have a valid name');
      }

      if (group.count <= 0) {
        throw new Error(`Unit group "${group.unitName}" must have a positive count`);
      }

      if (group.unitStrength <= 0) {
        throw new Error(`Unit group "${group.unitName}" must have positive strength`);
      }

      if (group.totalHealth <= 0) {
        throw new Error(`Unit group "${group.unitName}" must have positive health`);
      }
    }

    // Validate each enemy unit
    for (const enemyUnit of config.enemyUnits) {
      if (!enemyUnit.name || enemyUnit.name.trim() === '') {
        throw new Error('All enemy units must have a valid name');
      }

      if (enemyUnit.health <= 0) {
        throw new Error(`Enemy unit "${enemyUnit.name}" must have positive health`);
      }

      if (enemyUnit.strength < 0) {
        throw new Error(`Enemy unit "${enemyUnit.name}" cannot have negative strength`);
      }
    }
  }

  /**
   * Check if enemy unit battle should end
   */
  private shouldEnemyUnitBattleEnd(state: EnemyUnitBattleState): boolean {
    return state.playerGroups.length === 0 || state.battleEnded;
  }

  /**
   * Process a turn in enemy unit battle
   */
  private processEnemyUnitTurn(state: EnemyUnitBattleState, playerGoesFirst: boolean): void {
    // Get initial orders
    const initialPlayerAttackOrder = this.calculateAttackOrder(state.playerGroups);
    const initialPlayerCount = initialPlayerAttackOrder.length;
    const aliveEnemyUnits = state.enemyUnits.filter(e => e.isAlive);
    const maxEnemyTurns = aliveEnemyUnits.length;

    // Track attacks this phase
    const playerGroupsAttacked = new Set<string>();
    let enemyTurnsTaken = 0;

    const totalTurns = Math.max(initialPlayerCount, maxEnemyTurns);

    // Process alternating turns
    for (let turnIndex = 0; turnIndex < totalTurns * 2; turnIndex++) {
      if (this.shouldEnemyUnitBattleEnd(state)) {
        break;
      }

      const isPlayerTurn = playerGoesFirst ? (turnIndex % 2 === 0) : (turnIndex % 2 === 1);

      if (isPlayerTurn) {
        // Player's turn
        const nextPlayerGroup = this.getNextPlayerAttackerForEnemyBattle(
          state,
          initialPlayerAttackOrder,
          playerGroupsAttacked
        );
        
        if (nextPlayerGroup) {
          this.processSinglePlayerAttackOnEnemyUnit(state, nextPlayerGroup, playerGroupsAttacked.size);
          const groupId = `${nextPlayerGroup.unitName}:${nextPlayerGroup.count}:${nextPlayerGroup.unitStrength}`;
          playerGroupsAttacked.add(groupId);
        }
      } else {
        // Enemy's turn
        if (enemyTurnsTaken < aliveEnemyUnits.length && state.playerGroups.length > 0) {
          this.processSingleEnemyUnitAttack(state, enemyTurnsTaken);
          enemyTurnsTaken++;
        }
      }

      // Check if phase is complete
      if (playerGroupsAttacked.size >= initialPlayerCount && enemyTurnsTaken >= maxEnemyTurns) {
        break;
      }
    }
  }

  /**
   * Process player attack on enemy unit
   */
  private processSinglePlayerAttackOnEnemyUnit(state: EnemyUnitBattleState, attackingGroup: StackingGroup, attackIndex: number): void {
    if (state.playerGroups.length === 0) return;

    const aliveEnemyUnits = state.enemyUnits.filter(e => e.isAlive);
    if (aliveEnemyUnits.length === 0) return;

    const targetEnemyIndex = attackIndex % aliveEnemyUnits.length;
    const targetEnemyState = aliveEnemyUnits[targetEnemyIndex];
    const targetEnemy = targetEnemyState.unit;

    // Calculate damage (basic damage for now, could be enhanced with player modifiers)
    const baseDamage = BattleUtils.calculateDamageDealt(attackingGroup);
    
    // Create enhanced combat action
    const action: EnemyUnitCombatAction = {
      turn: state.currentTurn,
      attacker: BattleUtils.formatGroupForLog(attackingGroup),
      target: targetEnemy.name,
      action: `attack ${targetEnemy.name} and deal ${baseDamage.toLocaleString()} damage`,
      damageDealt: baseDamage,
      eliminated: false,
      enemyUnit: targetEnemy,
      effectiveStrength: attackingGroup.unitStrength
    };

    // Apply damage to enemy
    targetEnemyState.currentHealth = Math.max(0, targetEnemyState.currentHealth - baseDamage);
    targetEnemyState.damageReceived += baseDamage;

    // Check if enemy is defeated (though they never truly die in this system)
    if (targetEnemyState.currentHealth <= 0) {
      targetEnemyState.isAlive = false;
      action.action = `attack ${targetEnemy.name} and deal ${baseDamage.toLocaleString()} damage (defeated)`;
    }

    // Add to combat log and track damage
    state.combatLog.push(action);
    state.totalDamageDealt += baseDamage;
  }

  /**
   * Process enemy unit attack on player
   */
  private processSingleEnemyUnitAttack(state: EnemyUnitBattleState, enemyIndex: number): void {
    if (state.playerGroups.length === 0) return;

    const aliveEnemyUnits = state.enemyUnits.filter(e => e.isAlive);
    if (enemyIndex >= aliveEnemyUnits.length) return;

    const attackingEnemyState = aliveEnemyUnits[enemyIndex];
    const attackingEnemy = attackingEnemyState.unit;

    // Get target (highest health player group)
    const currentTargetOrder = this.calculateEnemyTargetOrder(state.playerGroups);
    if (currentTargetOrder.length === 0) return;

    const targetGroup = currentTargetOrder[0];

    // Calculate effective strength (including modifiers if enabled)
    let effectiveStrength = attackingEnemy.strength;
    const modifiersApplied: Array<{ type: string; value: number }> = [];

    if (state.useEnemyModifiers && attackingEnemy.attack_modifiers) {
      // For now, we'll apply a general modifier since StackingGroup doesn't have unit types
      // This could be enhanced by looking up the actual unit data to get unit types
      const generalModifier = attackingEnemy.attack_modifiers.reduce((total, mod) => total + mod.value, 0) / attackingEnemy.attack_modifiers.length;
      if (generalModifier > 0) {
        effectiveStrength += Math.round(generalModifier);
        modifiersApplied.push({ type: 'General', value: Math.round(generalModifier) });
      }
    }

    // Create enhanced combat action
    const action: EnemyUnitCombatAction = {
      turn: state.currentTurn,
      attacker: attackingEnemy.name,
      target: BattleUtils.formatGroupForLog(targetGroup),
      action: `attack and kill ${BattleUtils.formatGroupForLog(targetGroup)}`,
      eliminated: true,
      enemyUnit: attackingEnemy,
      effectiveStrength,
      modifiersApplied: modifiersApplied.length > 0 ? modifiersApplied : undefined
    };

    // Add modifier info to action description if applicable
    if (modifiersApplied.length > 0) {
      const modifierText = modifiersApplied.map(m => `+${m.value} vs ${m.type}`).join(', ');
      action.action += ` (${modifierText})`;
    }

    // Add to combat log
    state.combatLog.push(action);

    // Remove eliminated player group
    const groupIndex = state.playerGroups.findIndex(
      group => group.unitName === targetGroup.unitName &&
               group.count === targetGroup.count &&
               group.totalHealth === targetGroup.totalHealth
    );

    if (groupIndex !== -1) {
      state.playerGroups.splice(groupIndex, 1);
    }

    // Check if battle should end
    if (state.playerGroups.length === 0) {
      state.battleEnded = true;
    }
  }

  /**
   * Find the next player group that should attack (reimplemented for enemy battle)
   */
  private getNextPlayerAttackerForEnemyBattle(
    state: EnemyUnitBattleState,
    initialAttackOrder: StackingGroup[],
    alreadyAttacked: Set<string>
  ): StackingGroup | null {
    // Go through the initial attack order and find the first group that:
    // 1. Still exists in the current player groups
    // 2. Hasn't attacked yet this phase
    for (const originalGroup of initialAttackOrder) {
      const groupId = `${originalGroup.unitName}:${originalGroup.count}:${originalGroup.unitStrength}`;

      if (!alreadyAttacked.has(groupId)) {
        // Check if this group still exists (might have been eliminated)
        const currentGroup = state.playerGroups.find(g =>
          g.unitName === originalGroup.unitName &&
          g.unitStrength === originalGroup.unitStrength
        );

        if (currentGroup) {
          return currentGroup;
        }
      }
    }

    return null;
  }

  /**
   * Get enemy unit battle statistics
   */
  getEnemyUnitBattleStatistics(result: BattleResult): any {
    const baseStats = BattleUtils.calculateBattleStatistics(result.combatLog, result.totalDamageDealtToEnemies);
    
    // Enhanced statistics for enemy unit battles
    const enemyUnitActions = result.combatLog.filter(action => 
      (action as EnemyUnitCombatAction).enemyUnit !== undefined
    ) as EnemyUnitCombatAction[];

    const damageByEnemyUnit: Record<string, number> = {};
    const attacksByEnemyUnit: Record<string, number> = {};
    const modifiersUsed: Record<string, number> = {};

    enemyUnitActions.forEach(action => {
      if (action.enemyUnit) {
        const enemyName = action.enemyUnit.name;
        
        if (action.eliminated) {
          // Enemy attack
          attacksByEnemyUnit[enemyName] = (attacksByEnemyUnit[enemyName] || 0) + 1;
          
          // Track modifier usage
          if (action.modifiersApplied) {
            action.modifiersApplied.forEach(modifier => {
              const key = `${enemyName} vs ${modifier.type}`;
              modifiersUsed[key] = (modifiersUsed[key] || 0) + modifier.value;
            });
          }
        } else {
          // Player attack on enemy
          const damage = action.damageDealt || 0;
          damageByEnemyUnit[enemyName] = (damageByEnemyUnit[enemyName] || 0) + damage;
        }
      }
    });

    return {
      ...baseStats,
      damageByEnemyUnit,
      attacksByEnemyUnit,
      modifiersUsed,
      totalEnemyUnitsInvolved: Object.keys(attacksByEnemyUnit).length,
      totalModifierApplications: Object.keys(modifiersUsed).length
    };
  }
}